.welcome {
  background-image: url("../../assets/images/welcome-page/bg.png");
  width: 100%;
  height: 100vh;
  position: relative;
  align-items: center;
  display: flex;
  overflow: hidden;

  .img-bg {
    background-image: url("../../assets/images/welcome-page/background.png");
    width: 1571px;
    height: 969px;
    background-size: 100% 100%;
    position: relative;
    margin: 0 auto;
    animation-name: animation-bg;
    animation-duration: 1s;
  }

  .img-man {
    max-height: 100%;
    position: absolute;
    bottom: 0;
    left: 0;
    animation-name: animation-man;
    animation-duration: 1s;
  }

  .img-women {
    position: absolute;
    left: 354px;
    top: 17%;
    animation-name: animation-women;
    animation-duration: 1s;
  }

  .img-paper {
    background-image: url("../../assets/images/welcome-page/paper.png");
    position: absolute;
    width: 796px;
    height: 969px;
    background-size: 100% 100%;
    right: -50px;
    animation-name: animation-paper;
    animation-duration: 1s;
    overflow: hidden;

    .paper-title {
      font-size: 60px;

      height: 160px;
      margin-top: 150px;
      font-weight: 600;
      text-align: center;
      color: #241313;
      font-weight: bold;
      animation-name: animation-letter;
      animation-duration: 1s;
      animation-fill-mode: both;
    }

    .paper-description {
      display: flex;
      flex-flow: column;
      justify-content: space-between;
      animation-name: animation-letter;
      animation-duration: 1s;

      p {
        font-size: 19px;
        width: 519px;
        font-weight: bold;
        margin: 0 auto;
      }
    }
  }
}

@keyframes animation-bg {
  0% {
    margin-top: 200px;
    opacity: 0.5;
  }

  100% {
    margin-top: 0px;
    opacity: 1;
  }
}

@keyframes animation-paper {
  0% {
    margin-top: -30px;
    opacity: 0.5;
    margin-right: -100px;
  }

  100% {
    margin-top: 0;
    opacity: 1;
    margin-right: 0;
  }
}

@keyframes animation-letter {
  0% {
    opacity: 0.5;
    margin-right: -100px;
  }
  100% {
    opacity: 1;
    margin-right: 0;
  }
}

@keyframes animation-man {
  0% {
    opacity: 0.5;
    left: -300px;
  }

  100% {
    opacity: 1;
    left: 0;
  }
}

@keyframes animation-women {
  0% {
    opacity: 0.5;
    left: 200px;
  }

  100% {
    opacity: 1;
    left: 354px;
  }
}
