.marketplace {
  background-image: url("../../assets/images/marketplace-page/bg.png");
  width: 100%;
  height: 100vh;
  display: flex;
  overflow: hidden;
}

.market-body {
  width: 1168px;
  margin: 0 auto;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;

  .img-fire {
    position: absolute;
    height: 969px;
  }

  .features {
    position: relative;
    width: 722px;
    height: 722px;
    display: flex;
    justify-content: center;
    border-radius: 50%;
    align-items: center;
  }

  .img-elipse {
    position: relative;
  }

  .marketplace-button {
    background: url("../../assets/images/marketplace-page/buttom.png"),
      transparent;
    position: absolute;
    background-size: 100% 100%;
    width: 486px;
    height: 131px;
    border: none;
    top: -50px;
    font-size: 40px;
    font-weight: bold;
  }

  .img-elipse-bg1 {
    border-radius: 50%;
    position: absolute;
  }

  .img-warrior1 {
    position: absolute;
    left: -337px;
    bottom: 60px;
  }

  .img-warrior2 {
    position: absolute;
    right: -267px;
    bottom: 125px;
  }

  .img-warrior3 {
    position: absolute;
    left: -200px;
    bottom: 125px;
    height: 520px;
  }

  .img-warrior4 {
    position: absolute;
    right: -100px;
    bottom: 125px;
    height: 520px;
  }

  .marketplace-description {
    position: absolute;
    top: 500px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    p {
      color: white;
      text-align: center;
      max-width: 1232px;
      font-size: 20px;
      position: absolute;
      top: 100px;
    }
  }

  .img-arrow-right {
    width: 73px;
    height: 15px;
    display: flex;
    align-items: center;
  }

  .img-arrow-left {
    width: 73px;
    height: 15px;
    display: flex;
    align-items: center;
  }
}
