.subscribe {
  background: url("../../assets/images/home-page/bg.png");
  height: 100vh;
  width: 100%;
  overflow: hidden;
  position: relative;

  .img-degrade-subscribe {
    position: absolute;
    bottom: 0;
    z-index: 5;
    width: 100vw;
    left: 0;
  }

  .subscribe-header {
    background: url("../../assets/images/subscribe-page/Agrupar\ 30.png");
    position: relative;
    display: flex;
    justify-content: center;
    min-height: 371px;

    .subscribe-img-line {
      height: 100%;
      width: 100%;
      margin-top: -300px;
      opacity: 0.2;
    }

    .subscribe-form {
      position: absolute;
      top: 150px;

      p {
        font-size: 63px;
        font-weight: bold;
        color: rgb(66, 41, 10);
        padding: 0;
        margin: 0;
      }

      .test {
        animation-name: animation-subscribe-form-p;
        animation-duration: 1s;
        animation-fill-mode: both;
      }

      .subscribe-input {
        width: 80%;
        height: 50px;
        border-radius: 120px;
        border: none;
        color: rgb(66, 41, 10);
        font-size: 20px;
        padding-left: 50px;
        opacity: 0.7;
        min-width: 800px;
      }

      .subscribe-send {
        width: 10%;
        border: none;
        border-radius: 120px;
        font-size: 20px;
        font-style: italic;
        color: rgb(66, 41, 10);
        opacity: 0.7;
        min-width: 99px;
        height: 100%;
      }
    }
  }

  .subscribe-body {
    position: relative;
    height: calc(100% - 371px);

    .video-sunshine {
      position: absolute;
      right: -300px;
      top: 0;
      min-width: 100%;
      min-height: 100%;
      opacity: 0.3;
      // transform: rotate(180deg);
    }

    .img-mt5 {
      position: absolute;
      left: -150px;
      bottom: -160px;
    }

    .img-mt4 {
      position: absolute;
      left: 160px;
      bottom: -160px;
    }

    .img-mt2 {
      position: absolute;
      left: -50px;
      bottom: -200px;
    }

    .img-mt1 {
      position: absolute;
      right: 0;
      bottom: -200px;
      width: 100%;
    }

    .img-mt3 {
      position: absolute;
      bottom: -150px;
      right: 5vw;
    }

    .img-character {
      position: absolute;
    }

    .img-cloud2 {
      position: absolute;
      left: -400px;
      bottom: -300px;
    }

    .img-cloud3 {
      position: absolute;
      bottom: 200px;
      left: 500px;
    }

    .img-cloud1 {
      position: absolute;
      bottom: 73px;
      left: 200px;
    }
  }
}

@keyframes animation-subscribe-form-p {
  0% {
    margin-left: -200px;
  }

  100% {
    margin-left: 0;
  }
}
