.footer {
  background-color: black;
  display: flex;
  justify-content: space-around;
  padding: 200px 0;
  position: relative;

  .degrade {
    position: absolute;
    top: -360px;
    width: 100%;
  }

  .footer-left {
    .footer-contact img {
      padding: 15px;
    }
  }

  .footer-middle {
    display: flex;
    align-items: center;

    ul {
      list-style-type: none;
      display: flex;
      flex-wrap: wrap;
      width: 569px;

      height: 59px;
      align-items: center;

      li {
        width: 30%;

        a {
          text-decoration: none;
          color: white;
          font-size: 16px;
          display: flex;
        }
      }
    }
  }
}

.footer-logo {
  display: flex;
  justify-content: center;
}

.img-rubi {
  position: absolute;
  bottom: 30px;
  margin: 0 auto;
  z-index: 5;
  padding: 30px;
  background-color: black;
}

.img-footer-line {
  width: 100%;
  position: absolute;
  left: 0;
  z-index: 2;
  bottom: 80px;
}
